.chat{
    display: flex;
    flex-direction: column;
    flex: 0.75;
    background-color: #363a3f;
    height: 100vh;
}
.chat_messages{
    flex:1;
    overflow:scroll;
}
.chat_input{
    color:lightgray;
    display:flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    border-radius:5px;
    margin: 20px;
    border-top: 1px solid gray;
    background-color: #474b53;
    height: 50px;
}
.chat_input>form>input{
    padding: 15px;
    background:transparent;
    border:none;
    outline-width: 0;
    color: white;
    width:100%;
    font-size: large;
    overflow-x: auto;
    overflow-y: auto;

}
.chat_input>form{
    flex:1;
}
.chat_inputButton{
    display: none;
}
.chat_inputIcons>.MuiSvgIcon-root{
    padding: 5px;
}
.chat_messages{
    flex: 1;
}
.chat_messages::-webkit-scrollbar{
    display:none;
}
.chat_messages{
    -ms-overflow-style:none;
    scrollbar-width:none;
}
